/* VARIABLES */
:root {
	--font-title: 'abril fatface', serif;
  --font-text: 'assistant', sans-serif;
  --color-lightest: #ede6f0;
  --color-main: #f9afa1;
  --color-accent: #535c89;
	--color-second: #d6e3f4;
	--color-third: #cbe7de;
	--color-fourth: #ffcca9;
	--color-text: #59316d;
}


/* TYPE */

body {
  margin: 0;
  font-family: 'assistant', sans-serif;
  font-family: var(--font-text);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
  color: #59316d;
  color: var(--color-text);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
h1, h2, h3, h4, h5, h6 {
  font-family: inherit;
  font-size: inherit;
  font-weight: 300;
}

.type-l {font-size: 2rem;}
.type-m {font-size: 1.75rem}
.type-s {font-size: 1rem;}
.type-xs {font-size: 0.8rem;}

.type-display {font-family: 'abril fatface', serif;font-family: var(--font-title);}
.type-center {text-align: center;}

/* ATOMS */

.margin-top {
  margin-top: 5rem;
}
.margin-half-top {
  margin-top: 2.5rem;
}

.btn {
  padding: 10px 20px;
  background-color: #535c89;
  background-color: var(--color-accent);
  border-radius: 50px 50px 50px 0;
  color: white;
  text-decoration: none;
  font-weight: 800;
  text-transform: lowercase;
  font-size: 1.25rem;
  transition: linear 0.2s;
}

.RoomBtn {
  padding: 10px 20px !important;
  border: 0;
  background-color: transparent;
  border-radius: 50px;
  color: #535c89;
  color: var(--color-accent);
  border: 2px solid #535c89;
  border: 2px solid var(--color-accent)
}

.btn:hover {
  background-color: #59316d;
  background-color: var(--color-text);
  transition: linear 0.2s;
}

.btn.btn-center {
  margin-left: auto;
  margin-right: auto;
  display: inline-block;
}

.classImage {
  max-height: 150px;
  margin: 0 auto;
  display: block;
}

/* MOLECULES */

.main-wrapper {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  box-sizing: border-box;
  padding: 2rem
}
.main-wrapper > div {
  max-width: 40rem;
}

/* TEMPLATES */


/* BACKGROUNDS */

#RoomName {
  /*background-color: var(--color-main);*/
  padding: 2rem;
  height: 7rem;
  box-sizing: border-box;
  background: url(/EADLearningTool/static/media/capa-top.6df64334.svg);
  background-size: cover;
  background-position: bottom;
  text-align: center;
  font-family: 'abril fatface', serif;
  font-family: var(--font-title);
  font-size: 1.25rem;
}

#sala {
  height:calc(100vh - 7.6rem);
  top: 0;
}

.bg-shape-bottom {
  background-image: url(/EADLearningTool/static/media/formas-bottom-2.15b83aae.svg);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 90%;
}

@media screen and (max-width: 40rem) {
  .bg-shape-bottom {background-size: 70%;}
}

